* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.page {
  font-family: Roboto, sans-serif;
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  height: 406px;
  padding-top: 32px;
  padding-left: 16px;
  padding-right: 16px;
}

.card__top {
  padding-left: 3px;
  padding-right: 3px;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: round;
  background-size: 100%;
  width: 160px;
  height: 134px;
}

.card__description {
  color: #060b35;
  width: 166px;
  height: 36px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__description--margin--top {
  margin-top: 40px;
}

.card__code {
  color: #616070;
  width: 98px;
  height: 14px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__code--margin--top {
  margin-top: 4px;
}

.card__priceBlock {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  display: flex;
}

.card__priceBlock--margin--top {
  margin-top: 24px;
}

.card__txt {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.card__button--margin--top {
  margin-top: 16px;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 16px;
  display: flex;
}

.card__rating--margin--top {
  margin-top: 16px;
}

.card__rating .card__stars-area {
  gap: 4px;
  padding: 0;
  list-style: none;
  display: flex;
}

.card__rating .card__stars-area .card__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 16px;
  height: 16px;
}

.card__rating .card__stars-area .card__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__rating .card__reviews {
  color: #060b35;
  height: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}
